import React from 'react'
import {Link} from 'react-router-dom'

import publicUrl from './publicurl'

function ImagePreview(props){
  return (<figure className="image is-128x128 is-marginless is-paddingless">
            <Link to={props.to}>
              <img alt={props.name} src={props.src} />
            </Link>
          </figure>)
}

function PreviewSet(props){
  var amount = props.amount || 5,
      listing = props.listing || {}
  
  return Object.keys(listing)
                    .map((name,key)=>(
                      <div key={key} className="card">
                        <Link to={'/'+name}>
                          <h3 className="is-size-3 has-text-centered">
                            {name}
                          </h3>
                        </Link>
                        <div className="level is-mobile" style={{overflowX:'auto'}}>
                          {Object.values(listing[name])
                            .slice(
                                Math.floor(Math.random()*Object.keys(listing[name]).length)-amount
                                )
                            .slice(0,amount)
                            .map(
                            (src,key)=>( <div className="level-item" key={key}>
                                            <ImagePreview 
                                                  name={name} 
                                                  src={ publicUrl('wallpaper',src) } 
                                                  to={'/'+name+'/1/'+src.split('/')[1]}
                                                  />
                                          </div>)
                          )}
                        </div>
                      </div>))
}

function Home(props){
  var listing = props.listing || {},
      total = Object.values(listing).reduce((acc,collection)=>acc+=collection.length,0)

  return (<div className="container">
            <h2 className="is-size-2">Wallfall</h2>
              <p>
                Total of {total} wallpapers in {Object.keys(listing).length} collection(s).
              </p>
              <div>
                <PreviewSet listing={listing}/>
              </div>
          </div>)
                
}

export default Home


