import React, { Fragment } from 'react'
import {Link,NavLink} from 'react-router-dom'

function Pagination(props){
  var category  = props.category || '',
      index     = props.index || 1,
      max       = props.max   || 1,
      maxsteps  = props.steps || 3
  
  return (<div className="pagination" role="navigation" aria-label="pagination">
            
            {index > 1 ? <Link to={'/'+category+'/'+(index-1)} className="pagination-previous" >Previous</Link>:null}
            {index < max ? < Link to={'/'+category+'/'+(index+1)} className="pagination-next">Next page</Link>:null}
            
            <ul className="pagination-list">
            {index>=1
              ? ( <Fragment>
                    <li>
                        <Link to={'/'+category+'/1'} 
                            className={index===0?"pagination-link is-current":"pagination-link"}
                            aria-label={"Goto page 1"}>
                            1
                      </Link>
                    </li>
                    <li>
                      <span className="pagination-ellipsis">&hellip;</span>
                    </li>
                   </Fragment> )
              : (null) }
                    
              {new Array(maxsteps).fill(0).map((zero,subindex)=>
                index + subindex > 1 &&
                index + subindex < max
                
                ? (<li key={subindex}>
                  <NavLink className={index+1===(index+subindex)?"pagination-link is-current":"pagination-link"}
                            aria-label={"Goto page "+(index  + subindex)} 
                            to={'/'+category+'/'+(index + subindex)}>
                            {index  + subindex}
                  </NavLink>
                </li>)
                : ( null )
                )}
                
              {!index+1<=max
              ? ( <Fragment>
                  <li>
                    <span className="pagination-ellipsis">&hellip;</span>
                  </li>
                  <li>
                    <Link to={max.toString()} 
                      className={index+1===max?"pagination-link is-current":"pagination-link"}
                      aria-label={"Goto page "+max}>
                      {max}
                    </Link>
                  </li> 
                  </Fragment>)
              : ( null )
              }
              
            </ul>

          </div>)
}

export default Pagination


