import React from 'react'
import {Link} from 'react-router-dom'

import publicUrl from './publicurl'

function Lightbox(props){
  var category = props.params && props.params.category ? props.params.category : null,
      name     = props.params && props.params.name ? props.params.name : null
      
  return (<div className="modal is-active">
            <div className="modal-background"></div>
              <div className="modal-content">
                {category && name ?
                  (<figure className="image">
                    <Link to={publicUrl('wallpaper',category,name)}
                          download>
                    <img src={publicUrl('wallpaper',category,name)}
                         alt={name}/> 
                    </Link>
                  </figure>)
                  : (<div>
                      <p>Missing category or image name.</p>
                     </div>
                  )}
             </div>
            <button className="modal-close is-large" aria-label="close"
                    onClick={(e)=>e.target.parentNode.classList.remove('is-active')}></button>
          </div>)
}

export default Lightbox


